://www.are.na/karen-czock/dt-output\">are.na output channel</a></li></ol></div><ol class=\"mt-3 text-xs\"><h4 class=\"text-sm filter-outline-xs\">Collection</h4><li><a href=\"https://gardenblues.net/\" target=\"_blank\">Garden Blues</a></li></ol><h3 class=\"text-md filter-outline-xs mt-16 mb-3\">6. Further Reading</h3><a class=\"block\" target=\"_blank\" href=\"https://gist.github.com/melaniehoff/95ca90df7ca47761dc3d3d58fead22d4\">Always Already Programming (Melanie Hoff)</a><a class=\"block\" target=\"_blank\" href=\"https://www.are.na/blog/the-poetry-of-tools\">The Poetry of Tools (Mindy Seu)</a>", 15);
